html, body {
    height: 100%;
}

html {
    background: black;
}

body{
    position: absolute;

    background: #FFFFFF;
    border-radius: 24px;
    box-sizing: border-box;
    min-height: 100vh;
    height: auto;
    width: 100%;
    padding-bottom: 15px;
    overflow-y: scroll;
    overflow-x: hidden;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;

}


::-webkit-scrollbar-track {
    background: none;
  }
  ::-webkit-scrollbar {
    width: 10px;
    height: 10px;
  }
  ::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 5px;
  }

.wrapper{
    display: block;
    height: 100%;
    min-height: 100vh;
    width: 1242px;
    margin-left: auto;
    margin-right: auto;
}

.window__wrapper{
    display: block;
    visibility: hidden;
    position: absolute;
    z-index: 100;
    background: rgba(0,0,0, 0.5);
    height: 100%;
    width: 100%;
    opacity: 0;
    transition: visibility 300ms, opacity 300ms;

}

.appear{
    visibility: visible!important;
    opacity: 1!important;
    transition: visibility 300ms, opacity 300ms;
}

.card{
    position: fixed;
    background: #FFFFFF;
    border-radius: 12px;
    padding: 24px 36px 24px 32px;
    left: 50%;
